@use 'modern-normalize';

// normalize anchor tags
a,
a:active,
a:visited {
    color: inherit;
    text-decoration: none;
}

a[aria-current='page'] {
    color: inherit;
    text-decoration: none;
    cursor: not-allowed;
}

a:hover {
    color: inherit;
    text-decoration: none;
}

h1,
h2 {
    margin: 0;
}

em {
    font-style: normal;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

li {
    display: block;
}
