
                    @use "src/styles/breakpoints.scss" as *;
                    @use "src/styles/variables.scss" as *;
                
.mainPage {
    & &__lead {
        justify-content: end;
        background:
            url('./2xearth.png') no-repeat left 15px top -100px,
            linear-gradient($black-transparent, $grey-transparent),
            url('./4xgalaxy.png') no-repeat center center;
        background-size:
            auto 336px,
            100% 100%,
            cover;
    }
}
@include respond(md) {
    .mainPage {
        & &__lead {
            justify-content: center;
            // эти паддинги основаны на размерах фоновой картинки, поэтому в пикселях
            padding-right: 260px;
            background-position:
                right -100px top 25px,
                0 0,
                center center;
            background-size:
                auto 367px,
                100% 100%,
                cover;
        }
    }
}
@include respond(lg) {
    .mainPage {
        & &__lead {
            padding-right: 500px;
            background-position:
                right 90px top 60px,
                0 0,
                center center;
            background-size:
                auto 367px,
                100% 100%,
                cover;
        }
    }
}
